<!-- @format -->

<template>
  <div>
        <v-touch v-on:swiperight="onSwipeRight">
        <div class="main">
      <div class="center-view">
        <div class="avatar">
          <img v-if="!userInfo.userId"
            src="data:image/png;base64,"
            class="avator-icon"
          />
          <img v-if="userInfo.userId"
            :src="userInfo.headIcon"
            class="avator-icon"
          />
          <div v-if="userInfo.userId" class="nick-name">{{userInfo.mobile}}</div>
          <div v-if="!userInfo.userId" class="nick-name" @click="login">立即登录</div>
        </div>
        <ul class="my-order-tab">
          <li>
            <img
              src="data:image/png;base64,"
              width="26px"
              height="26px"
              alt=""
            />
            <span
              class="mint-badge is-primary is-size-small"
              style="display: none;"
              >99+</span
            >
            <div class="common-p">电影订单</div>
          </li>
          <li>
            <img
              src="data:image/png;base64,"
              width="26px"
              height="26px"
              alt=""
            />
            <div class="common-p">商品订单</div>
          </li>
        </ul>
        <!---->
        <div class="margin-set my-card">
          <img
            src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACgAAAAoBAMAAAB+0KVeAAAAIVBMVEVHcEz/iGT/hWL/hGL/hWL/hGL/hGL/hGP/hWP/jGn/hGISk7EWAAAACnRSTlMAHOamtu21p7QdUXNikQAAAFtJREFUKM9jYKAFENdahQIWGTIwMEatQgNLBRiYlwig6mT0MmBgWohu3iwFBq4F6IJAEZoIrsIAuATp5qTBI8i2atUybIIraGU7ZmKQUsCebLAmMMykWEiTFA8AIQBrdTyC6ekAAAAASUVORK5CYII="
            width="20px"
            height="20px"
            alt=""
          />
          <span class="label">卖座券</span>
          <img
            src="data:image/png;base64,"
            alt=""
            class="arrow"
          />
        </div>
        <div class="margin-set my-card">
          <img
            src="data:image/png;base64,"
            width="20px"
            height="20px"
            alt=""
          />
          <span class="label">组合红包</span>
          <img
            src="data:image/png;base64,"
            alt=""
            class="arrow"
          />
        </div>
        <div class="margin-set my-balance">
          <img
            src="data:image/png;base64,"
            width="20px"
            height="20px"
            alt=""
          />
          <span class="label">余额</span>
          <span class="value"
            ><span class="price-decimal price-fmt"
              ><i>￥</i>
              <span class="interge" style="font-size: 15px;">0</span>
              <span style="display: none;">.</span>
              <span class="decimal" style="font-size: 10px;"></span>
              <span style="display: none;">.</span>
              <span class="decimal" style="font-size: 10px; display: none;"
                >00</span
              >
            </span></span
          >
          <img
            src="data:image/png;base64,"
            alt=""
            class="arrow"
          />
        </div>
        <div class="margin-set system-set">
          <img
            src="data:image/png;base64,"
            width="20px"
            height="20px"
            alt=""
          />
          <span class="label">设置</span>
          <img
            src="data:image/png;base64,"
            alt=""
            class="arrow"
          />
        </div>
        <div class="margin-set system-set">
          <img
            src="data:image/png;base64,"
            width="20px"
            height="20px"
            alt=""
          />
          <span class="label" @click="exit">退出</span>
          <img
            src="data:image/png;base64,"
            alt=""
            class="arrow"
          />
        </div>
      </div>
    </div>
        </v-touch>  
    
  </div>
</template>

<script>
import{userInfo} from '@/api/api'
export default {
  data() {
    return {
      userInfo:{}
    }
  },
  async mounted(){
    let _token = this.$store.state._token
    if(_token){
    let ret = await userInfo(_token);
    this.userInfo = ret.data.user_info
    }
    
  },
  methods: {
    login: function() {
      this.$router.push({ path: 'login' });
    },
    exit:function(){
      localStorage.removeItem('_token')
      this.$router.push({path:'login'})
    },
    onSwipeRight:function(){
      this.$router.push({path:'cinema'})
    }
  },
  beforeDestroy(){
    this.$router.go(0)
  }
};
</script>

